@prefixProgressClass: ant-progress;

.@{prefixProgressClass} {
  &-line-wrap,
  &-circle-wrap {
    display: inline-block;
  }

  &-line-wrap {
    font-size: 12px;
  }
  &-line-inner {
    display: inline-block;

    svg {
      vertical-align: middle;
    }
  }

  &-line-text {
    display: inline-block;
    font-size: 1em;
    margin-left: 10px;
    line-height: 1;

    .anticon {
      font-size: 12px;
    }
  }


  &-circle-inner {
    position: relative;
    line-height: 1;
  }
  &-circle-text {
    display: block;
    position: absolute;
    width: 100%;
    text-align: center;
    line-height: 1;
    top: 50%;
    transform: translateY(-50%);
    left: 0;

    .anticon {
      font-size: 14/12em;
    }
  }
}



